import React from 'react'
import './coinsearch.css'
const Coinsearch = ({setSearch,setSearclAll}) => {
    const handleChange = e => {
        setSearch(e.target.value)
    }

    return (
        <div className = 'coinsearch'>
            <form action="" className = 'coinsearch__form'>
                <label htmlFor="">Search in page</label>
                <input type="text" 
                    placeholder = 'Search in page'
                    className = 'coinsearch__input'
                    onChange = {handleChange}
                />
                <label htmlFor="">Search all</label>
                <input type="text" 
                    placeholder = 'Search all'
                    className = 'coinsearch__input--searchall'
                    onChange = {handleChange}
                />                
            </form>
        </div>
    )
}

export default Coinsearch
